@use 'base/mixins' as m;
@use 'base/variables' as v;


.boton-regresar {
    position: absolute;
    right: 1rem;
    top: 1rem;
    display: flex;
    justify-content: flex-end;
    font-size: 1.5rem;

    @media (width<768px) {
        justify-content: center;
        top: 8rem;
    }

}


.volver-ver-tickets {
    @include m.boton(v.$azul-siteur, v.$blanco, pulsate-azul-siteur);
    padding: 0 0 0 2rem;

    display: flex;
    align-items: center;
    gap: 2rem;

    width: 20rem;
    //  font-size: 2rem !important;
}


.boton-verde-limon {
    @include m.boton(#9eec50, white, pulsate-verde-limon);
}

.boton-rojo {
    @include m.boton(v.$rojo, white, pulsate-rojo);

}


.boton-azul {
    @include m.boton(#108dbf, white, pulsate-azul);
    margin-bottom: .5rem;

}

.boton-verde-limon,
.boton-rojo,
.boton-azul,
.botonNuevo {
    border-radius: 0;
    padding: .5rem 3rem;
}

.botonNuevo{
    @include m.boton(green, white, pulsate-green);
    margin-bottom: .5rem;
    width: fit-content;

}

.paginacion .boton-azul{
    width: 20rem;
    margin-bottom: 2rem;
    border-radius: .5rem;
}

.botonPaginaActivo{
    color: red;
}

.paginacion{
    display: flex;
}


.btn-primary{
    background-color: v.$azul-siteur!important;
    border-color: v.$azul-siteur!important;
    
}
.btn-outline-primary{
    border-color: v.$azul-siteur!important;
    color: v.$azul-siteur!important;
}
.btn-outline-primary:hover{
    background-color: v.$azul-siteur!important;
    color: white!important;
}